








































































import { Component, Vue } from "vue-property-decorator";
import axios from "axios";
@Component({
  components: {}
})
export default class NetworkHealth extends Vue {
  timer: any = null;
  leftBottom: any = {};
  leftTop: any = {};
  leftCenter: any = {};

  rightBottom: Array<any> = [];
  rightTop: Array<any> = [];
  rightTopTwo: Array<any> = [];
  show: boolean = true;
  showSecond: boolean = true;
  first: boolean = true;

  listenScreen() {
    window.addEventListener("resize", () => {
      (this as any).$rem.rem();
    });
  }
  // generateNumber(min: number, max: number) {
  //   return Math.floor(Math.random() * (max - min)) + min;
  // }

  getData(name: String) {
    (this as any).alarmTab = name;
    ["leftTop", "leftCenter", "leftBottom"].forEach(item => {
      this[item] = (this as any).data[name == "商密网" ? "bussiness" : "network"][item];
      (this as any).$refs[item].initData(this[item]);
    });
    this.rightBottom = (this as any).data[
      name == "商密网" ? "bussiness" : "network"
    ].rightBottom;
    //计算年度指标
    let temp = [];
    this.leftTop.data.forEach(element => {
      temp = temp.concat(element);
    });
    let total =
      temp.reduce((a, b) => {
        return a + b;
      }) / temp.length;
    (this as any).$refs.leftTopPanel.changeData(0, total, "%");
    //初始化地图
    if (this.first) {
      let names = ["互联网", "商密网"];
      for (let i = 0; i < names.length; i++) {
      axios.get("./" + (names[i] == '商密网' ? 'bussiness' : 'network') + ".json").then((res: any) => {
        (this as any).$refs[
          names[i] == "商密网" ? "rightTopSecond" : "rightTop"
        ].initData(
          res.data.rightTop,
          res.data.rightTopTwo,
          (this as any).data[names[i] == "商密网" ? "bussiness" : "network"].map
        );
      });
      }
     
      this.first = false;
    } else {
      if (name == "商密网") {
        this.showSecond = true;
        this.show = false;
      } else {
        this.showSecond = false;
        this.show = true;
      }
    }
   
    clearTimeout(this.timer);

    this.timer = setTimeout(() => {
      if ((this as any).alarmTab == "商密网") {
        this.getData("互联网");
      } else {
        this.getData("商密网");
      }
    }, this.$time);
  }
  beforeCreate() {
    this["title"] = "";
    this["data"] = null;
    this["serviceTab"] = "商密网";
    this["alarmTab"] = "商密网";
  }
  mounted() {
    (this as any).$rem.rem();
    (this as any).title = (this as any).$title_1;
    (this as any).data = (this as any).$data_center;

    this.listenScreen();
    this.getData("互联网");
  }
}
